'use client';

import { usePathname, useSearchParams } from 'next/navigation';
import { useLocale } from 'next-intl';
import { useState, useEffect } from 'react';
import type { Language } from '@/types';

/**
 * èªè¨€åˆ‡æ›å™¨ç›¸é—œè³‡æ–™çš„è‡ªå®šç¾© Hook
 */
export function useLanguageSwitcher() {
    // å–å¾—ç•¶å‰è·¯å¾‘ï¼Œç”¨æ–¼ç”Ÿæˆèªè¨€åˆ‡æ›é€£çµ
    const pathname = usePathname() || '/';
    // å–å¾—ç•¶å‰èªç³»
    const currentLocale = useLocale();
    // å–å¾— URL æŸ¥è©¢åƒæ•¸ï¼Œç”¨æ–¼ä¿æŒåˆ‡æ›èªè¨€æ™‚çš„åƒæ•¸
    const searchParams = useSearchParams();

    // èªè¨€åˆ—è¡¨ç‹€æ…‹
    const [languages, setLanguages] = useState<Language[]>([]);
    // è¼‰å…¥ç‹€æ…‹
    const [isLoading, setIsLoading] = useState(true);
    // éŒ¯èª¤ç‹€æ…‹
    const [error, setError] = useState<string | null>(null);

    // è¼‰å…¥èªè¨€åˆ—è¡¨
    useEffect(() => {
        let isMounted = true;

        const loadLanguages = async () => {
            try {
                setIsLoading(true);
                setError(null);

                // ä½¿ç”¨å®¢æˆ¶ç«¯ API è·¯ç”±ä¾†ç²å–èªè¨€åˆ—è¡¨
                const response = await fetch('/api/languages');
                if (!response.ok) {
                    throw new Error('Failed to fetch languages');
                }
                const languageList = await response.json();

                if (isMounted) {
                    setLanguages(languageList);
                }
            } catch (err) {
                if (isMounted) {
                    setError(err instanceof Error ? err.message : 'è¼‰å…¥èªè¨€åˆ—è¡¨å¤±æ•—');
                    console.error('Failed to load languages:', err);
                }
            } finally {
                if (isMounted) {
                    setIsLoading(false);
                }
            }
        };

        loadLanguages();

        return () => {
            isMounted = false;
        };
    }, []);

    return {
        pathname,
        currentLocale,
        searchParams,
        languages,
        isLoading,
        error,
    };
}